import React, { Component } from "react";
import TopLine from "./topline";
import NavBar from "./navbar";
import BottomBtn from "./bottomBtn";
import { Redirect } from "react-router-dom";
import closeimg from "../../img/cancel-music.png";

class AddReceiver extends Component {
  state = {
    backLink: "",
    redirectback: false,
    doUnmount: false,
    item: {
      title: "",
      link: "",
      amount: "",
      description: "",
      type_delivery: 0
    },
    redirect: false
  };

  openPopup = e => {
    var divs = document.getElementsByClassName("popup");
    for (var i = 0; i < divs.length; i++) {
      divs[i].style.display = "none";
    }
    let id = e.target.dataset.popup;
    document.getElementById(id).style.display = "block";
  };

  closePopup = e => {
    e.target.parentNode.parentNode.parentNode.parentNode.style.display = "none";
  };

  handleChange = ({ currentTarget: input }) => {
    const item = { ...this.state.item };
    item[input.name] = input.value;
    this.setState({ item });
  };

  goBackCustom = () => {
    let link = sessionStorage
      .getItem("link")
      .split("/")
      .pop();
    let session = sessionStorage.getItem("link").split("/");

    let newSession = [];

    session.map((i, index) => {
      if (index !== session.length - 1) {
        newSession.push(i);
      }
    });

    this.setState({
      backLink: `/` + link,
      redirectback: true,
      doUnmount: true
    });

    sessionStorage.setItem("link", newSession.join("/"));
  };

  componentWillUnmount() {
    let history = sessionStorage.getItem("link");
    if (!this.state.doUnmount) {
      history += this.props.location.pathname;
    } else {
      history = sessionStorage.getItem("link");
    }
    sessionStorage.setItem("link", history);
  }

  render() {
    if (this.state.redirectback) {
      return <Redirect to={this.state.backLink} />;
    }
    if (this.state.redirect) {
      return <Redirect to="/cart" />;
    }
    return (
      <div className="main-wrap page">
        <div
          className="wrong-pass-popup"
          style={{ display: this.state.display }}
        >
          {this.state.popupText}
        </div>
        <div className="popup" id="popup-title">
          <div className="close-popup-wrap">
            <div className="close-popup" onClick={this.closePopup}>
              <div className="cp-img">
                <img src={closeimg} alt="#" />
              </div>
            </div>
          </div>
          Название товара это ...
        </div>
        <div className="popup" id="popup-link">
          <div className="close-popup-wrap">
            <div className="close-popup" onClick={this.closePopup}>
              <div className="cp-img">
                <img src={closeimg} alt="#" />
              </div>
            </div>
          </div>
          Ссылка на товар это ...
        </div>
        <div className="popup" id="popup-amount">
          <div className="close-popup-wrap">
            <div className="close-popup" onClick={this.closePopup}>
              <div className="cp-img">
                <img src={closeimg} alt="#" />
              </div>
            </div>
          </div>
          Количество товаров это ...
        </div>
        <div className="popup" id="popup-desc">
          <div className="close-popup-wrap">
            <div className="close-popup" onClick={this.closePopup}>
              <div className="cp-img">
                <img src={closeimg} alt="#" />
              </div>
            </div>
          </div>
          Пожелания это ...
        </div>
        <div className="popup" id="popup-delivery">
          <div className="close-popup-wrap">
            <div className="close-popup" onClick={this.closePopup}>
              <div className="cp-img">
                <img src={closeimg} alt="#" />
              </div>
            </div>
          </div>
          Тип доставки это ...
        </div>
        <TopLine pageTitle="Добавить адрес" handleBack={this.goBackCustom} />
        <div className="addToCart-wrap container">
          <div className="input-wrap-add">
            <input
              type="text"
              name="title"
              placeholder="Название товара"
              onFocus={e => (e.target.placeholder = "")}
              onBlur={e => (e.target.placeholder = "Название товара")}
              onChange={this.handleChange}
            />
            <i
              className="far fa-question-circle"
              onClick={this.openPopup}
              data-popup="popup-title"
            ></i>
          </div>
          <div className="input-wrap-add">
            <input
              type="text"
              name="link"
              placeholder="Ccылка на товар"
              onFocus={e => (e.target.placeholder = "")}
              onBlur={e => (e.target.placeholder = "Ccылка на товар")}
              onChange={this.handleChange}
            />
            <i
              className="far fa-question-circle"
              onClick={this.openPopup}
              data-popup="popup-link"
            ></i>
          </div>
          <div className="input-wrap-add">
            <input
              type="number"
              name="amount"
              placeholder="Количество"
              onFocus={e => (e.target.placeholder = "")}
              onBlur={e => (e.target.placeholder = "Количество")}
              onChange={this.handleChange}
            />
            <i
              className="far fa-question-circle"
              onClick={this.openPopup}
              data-popup="popup-amount"
            ></i>
          </div>
          <div className="input-wrap-add">
            <input
              type="text"
              placeholder="Пожелания"
              name="description"
              onFocus={e => (e.target.placeholder = "")}
              onBlur={e => (e.target.placeholder = "Пожелания")}
              onChange={this.handleChange}
            />
            <i
              className="far fa-question-circle"
              onClick={this.openPopup}
              data-popup="popup-desc"
            ></i>
          </div>
          <div className="input-wrap-add">
            <select
              name="#"
              id="#"
              name="type_delivery"
              onChange={this.handleChange}
            >
              <option value="1">test</option>
              <option value="2">test1</option>
            </select>
            <i
              className="far fa-question-circle"
              onClick={this.openPopup}
              data-popup="popup-delivery"
            ></i>
          </div>
        </div>
        <BottomBtn btnTitle="Добавить" onClick={this.handleSubmit} />
      </div>
    );
  }
}

export default AddReceiver;
