import React, { Component } from "react";
import TopLine from "./common/topline";
import NavBar from "./common/navbar";
import { Redirect } from "react-router-dom";
import BottomBtn from "./common/bottomBtn";

class Contacts extends Component {
  state = {
    redirectback: false,
    backLink: "",
    doUnmount: false,
    item: {
      name: "",
      last_name: "",
      email: "",
      country_id: "",
      city_id: "",
      avatar: "",
      type_delivery: 0
    },
    popupText: "",
    display: "none",
    counties: [],
    cities: [],
    currCountry: 1,
    client: [],
    avaRef: React.createRef()
  };

  goBackCustom = () => {
    let link = sessionStorage
      .getItem("link")
      .split("/")
      .pop();
    let session = sessionStorage.getItem("link").split("/");

    let newSession = [];

    session.map((i, index) => {
      if (index !== session.length - 1) {
        newSession.push(i);
      }
    });

    this.setState({
      backLink: `/` + link,
      redirectback: true,
      doUnmount: true
    });

    sessionStorage.setItem("link", newSession.join("/"));
  };

  componentWillMount() {
    if (!sessionStorage.getItem("link")) {
      sessionStorage.setItem("link", "");
    }

    let formData2 = new FormData();
    formData2.append(
      "token",
      localStorage.getItem("token").replace(/[^\w\s]/gi, "")
    );
    formData2.append(
      "token_id",
      localStorage.getItem("tokenID").replace(/[^\w\s]/gi, "")
    );

    fetch("https://dev.crm.inta.group/app/userinfo", {
      method: "POST",
      body: formData2
    })
      .then(response => {
        return response.json();
      })
      .then(data => {
        if (JSON.stringify(data.error)) {
          console.log("error");
        } else {
          //console.log(data);
          this.setState({ client: data });
          this.state.item.name = this.state.client.first_name;
          this.state.item.last_name = this.state.client.last_name;
          this.state.item.email = this.state.client.email;
          this.state.item.country_id = this.state.client.country_id;
          this.state.item.city_id = this.state.client.city_id;
          this.state.item.type_delivery = this.state.client.type_delivery;
          this.state.item.avatar = this.state.client.avatar;
          // this.state.avaRef.current.value = this.state.client.avatar;

          this.getCity(this.state.client.country_id);
        }
      })

      .catch(err => console.log(err));

    let formData = new FormData();
    formData.append(
      "token",
      localStorage.getItem("token").replace(/[^\w\s]/gi, "")
    );
    formData.append(
      "token_id",
      localStorage.getItem("tokenID").replace(/[^\w\s]/gi, "")
    );

    fetch("https://dev.crm.inta.group/app/country", {
      method: "POST",
      body: formData
    })
      .then(response => {
        return response.json();
      })
      .then(data => {
        if (JSON.stringify(data.error)) {
          console.log("error");
        } else {
          //console.log(data);
          this.setState({
            counties: [{ id: 0, name_ru: "Выберете страну" }, data[0]]
          });
        }
      })
      .catch(err => console.log(err));

    ///////////////////////////////////////////
  }

  componentWillUnmount() {
    let history = sessionStorage.getItem("link");
    if (!this.state.doUnmount) {
      history += this.props.location.pathname;
    } else {
      history = sessionStorage.getItem("link");
    }
    sessionStorage.setItem("link", history);
  }

  getCity = e => {
    let formData1 = new FormData();
    formData1.append(
      "token",
      localStorage.getItem("token").replace(/[^\w\s]/gi, "")
    );
    formData1.append(
      "token_id",
      localStorage.getItem("tokenID").replace(/[^\w\s]/gi, "")
    );

    formData1.append("country_id", e);

    fetch("https://dev.crm.inta.group/app/city", {
      method: "POST",
      body: formData1
    })
      .then(response => {
        return response.json();
      })
      .then(data => {
        if (JSON.stringify(data.error)) {
          console.log("error");
        } else {
          //console.log(data);
          this.setState({ cities: data });
        }
      })
      .catch(err => console.log(err));
  };

  handleChange = ({ currentTarget: input }) => {
    const item = { ...this.state.item };
    item[input.name] = input.value;
    this.setState({ item });

    if (input.id === "country") {
      this.getCity(input.value);
    }
  };

  handleSubmit = () => {
    // -----------

    let formData = new FormData();
    formData.append(
      "token",
      localStorage.getItem("token").replace(/[^\w\s]/gi, "")
    );
    formData.append(
      "token_id",
      localStorage.getItem("tokenID").replace(/[^\w\s]/gi, "")
    );
    formData.append("first_name", this.state.item.name);
    formData.append("last_name", this.state.item.last_name);
    formData.append("email", this.state.item.email);
    formData.append("type_delivery", this.state.item.type_delivery);
    formData.append("country_id", this.state.item.country_id);
    formData.append("city_id", this.state.item.city_id);
    formData.append("avatar", this.state.avaRef.current.value);

    fetch("https://dev.crm.inta.group/app/userinfoedit", {
      method: "POST",
      body: formData
    })
      .then(response => {
        return response.json();
      })
      .then(data => {
        if (JSON.stringify(data.error)) {
          this.setState({ display: "block", popupText: "Ошибка" });
          setTimeout(() => {
            this.setState({ display: "none" });
          }, 2000);
        } else {
          this.setState({
            display: "block",
            popupText: "Профиль успешно отредактиорван"
          });
          setTimeout(() => {
            this.setState({ display: "none" });
          }, 2000);
        }
      })
      .catch(err => console.log(err));
  };

  handleFileSelect = evt => {
    var f = document.getElementById("file").files[0];
    var reader = new FileReader();

    reader.onload = (theFile => {
      return e => {
        var binaryData = e.target.result;

        var base64String = window.btoa(binaryData);

        document.getElementById("ava").value = base64String;
      };
    })(f);

    reader.readAsBinaryString(f);
  };

  render() {
    if (this.state.redirectback) {
      return <Redirect to={this.state.backLink} />;
    }

    return (
      <div className="main-wrap page">
        <TopLine
          pageTitle="Контактная информация"
          handleBack={this.goBackCustom}
        />
        <div
          className="wrong-pass-popup"
          style={{ display: this.state.display }}
        >
          {this.state.popupText}
        </div>
        <div className="cart-item-wrap container">
          <div className="pf-form">
            <label htmlFor="sum">Имя:</label>
            <input
              type="text"
              defaultValue={this.state.client.first_name}
              onChange={this.handleChange}
              name="name"
            />

            <label htmlFor="sum">Фамилия:</label>
            <input
              type="text"
              defaultValue={this.state.client.last_name}
              onChange={this.handleChange}
              name="last_name"
            />

            <label htmlFor="sum">E-mail:</label>
            <input
              type="text"
              defaultValue={this.state.client.email}
              onChange={this.handleChange}
              name="email"
            />

            <label
              htmlFor="sum"
              onClick={this.setAvatar}
              style={{ marginBottom: "20px" }}
            >
              Аватар
            </label>
            <input
              type="file"
              defaultValue={this.state.client.avatar}
              onChange={this.handleFileSelect}
              name="avatar"
              id="file"
            />

            <input type="hidden" id="ava" ref={this.state.avaRef} />

            <label htmlFor="sum">Способ доставки</label>
            <select
              onChange={this.handleChange}
              name="type_delivery"
              id="#"
              className="contact-select"
            >
              <option
                value="0"
                selected={
                  this.state.client.type_delivery === 0 ? "selected" : ""
                }
              >
                Выберете способ доставки
              </option>
              <option
                value="1"
                selected={
                  this.state.client.type_delivery === 1 ? "selected" : ""
                }
              >
                Авиа
              </option>
              <option
                value="2"
                selected={
                  this.state.client.type_delivery === 2 ? "selected" : ""
                }
              >
                Море
              </option>
              <option
                value="3"
                selected={
                  this.state.client.type_delivery === 3 ? "selected" : ""
                }
              >
                ЖД
              </option>
            </select>

            <label htmlFor="sum">Выберете страну</label>
            <select
              name="#"
              id="country"
              className="contact-select"
              onChange={this.handleChange}
              name="country_id"
              defaultValue={this.state.client.country_id}
            >
              {this.state.counties.map(country => {
                return (
                  <option
                    value={country.id}
                    key={country.id}
                    selected={
                      this.state.client.country_id === country.id
                        ? "selected"
                        : ""
                    }
                  >
                    {country.name_ru}
                  </option>
                );
              })}
            </select>

            <label htmlFor="sum">Выберете город</label>
            <select
              name="#"
              id="#"
              className="contact-select"
              onChange={this.handleChange}
              name="city_id"
            >
              {this.state.cities.map(city => {
                return (
                  <option
                    value={city.id}
                    key={city.id}
                    selected={
                      this.state.client.city_id === city.id ? "selected" : ""
                    }
                  >
                    {city.name_ru}
                  </option>
                );
              })}
            </select>
          </div>
        </div>
        <BottomBtn btnTitle="Сохранить" onClick={this.handleSubmit} />
      </div>
    );
  }
}

export default Contacts;
