import React, { Component } from "react";
import logo from "../img/logo.png";
import plane from "../img/airplane-shape.svg";
import goodsBuyback from "../img/goods-buyback.png";
import NavBar from "../components/common/navbar";
import { Link } from "react-router-dom";
import trainImg from "../img/train-front.svg";
import shipImg from "../img/sea-ship-with-containers.svg";
import cargoTruck from "../img/cargo-truck.svg";
import planet from "../img/planet.png";
import coure from "../img/coure-2.png";

class Home extends Component {
  state = {
    topLineRef: React.createRef(),
    topLineHeight: 0,
    backLink: "",
    redirectback: false,
    doUnmount: false,
    balance: 0
  };

  goBackCustom = () => {
    let link = sessionStorage
      .getItem("link")
      .split("/")
      .pop();
    let session = sessionStorage.getItem("link").split("/");

    let newSession = [];

    session.map((i, index) => {
      if (index !== session.length - 1) {
        newSession.push(i);
      }
    });

    this.setState({
      backLink: `/` + link,
      redirectback: true,
      doUnmount: true
    });

    sessionStorage.setItem("link", newSession.join("/"));
  };

  componentWillUnmount() {
    let history = sessionStorage.getItem("link");
    if (!this.state.doUnmount) {
      history += this.props.location.pathname;
    } else {
      history = sessionStorage.getItem("link");
    }
    sessionStorage.setItem("link", history);
  }

  componentDidMount() {
    this.setState({
      topLineHeight: this.state.topLineRef.current.clientHeight
    });
    if (!sessionStorage.getItem("link")) {
      sessionStorage.setItem("link", "");
    }

    let formData = new FormData();
    formData.append(
      "token",
      localStorage.getItem("token").replace(/[^\w\s]/gi, "")
    );
    formData.append(
      "token_id",
      localStorage.getItem("tokenID").replace(/[^\w\s]/gi, "")
    );

    fetch("https://dev.crm.inta.group/app/payments", {
      method: "POST",
      body: formData
    })
      .then(response => {
        return response.json();
      })
      .then(data => {
        this.setState({
          balance: data.balance
        });
        //console.log(this.state);
      })
      .catch(err => console.log(err));
  }

  render() {
    return (
      <div className="main-wrap page" style={{ overflow: "hidden" }}>
        <div className="home-top-line" ref={this.state.topLineRef}>
          <ul>
            <li>
              <Link to="/finance" className="home-top-line-list-content">
                <i className="fas fa-wallet" style={{ fontSize: "16px" }}></i>
                <br /> {this.state.balance}$
              </Link>
            </li>
            <li>
              <Link to="/helper" className="home-top-line-list-content">
                <img src={logo} alt="logo" />
                <br />
                <span>flimcor support</span>
              </Link>
              <div className="sup-status" />
            </li>
            <li>
              <Link to="/profile" className="home-top-line-list-content">
                <i className="fas fa-user" style={{ fontSize: "16px" }}></i>
                <br />
                Профиль
              </Link>
            </li>
          </ul>
        </div>

        <div
          className="home-wrap"
          style={{
            height: `calc(100vh - ${this.state.topLineHeight + 50}px)`
          }}
        >
          <div className="home-menu">
            <div className="menu-column">
              <Link to="/sea" className="menu-item">
                <div className="menu-item-content">
                  <img src={shipImg} alt="#" />
                  <span>Море</span>
                </div>
              </Link>

              <Link
                to="/truck"
                className="menu-item"
                style={{ marginTop: `20px`, transform: `translateX(10px)` }}
              >
                <div className="menu-item-content">
                  <img src={cargoTruck} alt="#" />
                  <span>Авто</span>
                </div>
              </Link>
            </div>

            <div className="menu-column">
              <Link to="/avia" className="menu-item">
                <div className="menu-item-content">
                  <img src={plane} alt="#" />
                  <span>Авиа</span>
                </div>
              </Link>

              <Link
                to="/logistic"
                className="menu-item menu-item-white"
                style={{ marginTop: `20px` }}
              >
                <div className="menu-item-content">
                  <img src={goodsBuyback} alt="#" />
                  <span>Выкуп товаров</span>
                </div>
              </Link>

              <Link
                to="/coure"
                className="menu-item"
                style={{ marginTop: `20px` }}
              >
                <div className="menu-item-content">
                  <img src={coure} alt="#" />
                  <span>Вызов курьера</span>
                </div>
              </Link>
            </div>

            <div className="menu-column">
              <Link to="/train" className="menu-item">
                <div className="menu-item-content">
                  <img src={trainImg} alt="#" />
                  <span>ЖД</span>
                </div>
              </Link>

              <Link
                to="/finance"
                className="menu-item"
                style={{ marginTop: `20px`, transform: `translateX(-10px)` }}
              >
                <div className="menu-item-content">
                  <img src={planet} alt="#" />
                  <span>Переводы 0%</span>
                </div>
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
