import React, { Component } from "react";
import TopLine from "./common/topline";
import { Redirect } from "react-router-dom";
import HelperBottom from "./common/helperBottom";
import logo from "../img/logo.png";
import triangle from "../img/triangle.png";

class Helper extends Component {
  state = {
    backLink: "",
    redirectback: false,
    doUnmount: false,
    question: ""
  };

  getQuest = e => {
    this.setState({ question: e.target.getAttribute("data-quest") });
  };

  goBackCustom = () => {
    let link = sessionStorage
      .getItem("link")
      .split("/")
      .pop();
    let session = sessionStorage.getItem("link").split("/");

    let newSession = [];

    session.map((i, index) => {
      if (index !== session.length - 1) {
        newSession.push(i);
      }
    });

    this.setState({
      backLink: `/` + link,
      redirectback: true,
      doUnmount: true
    });

    sessionStorage.setItem("link", newSession.join("/"));
  };

  answerQuest = e => {
    document.getElementById(this.state.question).style.display = "block";
  };

  componentWillUnmount() {
    let history = sessionStorage.getItem("link");
    if (!this.state.doUnmount) {
      history += this.props.location.pathname;
    } else {
      history = sessionStorage.getItem("link");
    }
    sessionStorage.setItem("link", history);
  }

  render() {
    if (this.state.redirectback) {
      return <Redirect to={this.state.backLink} />;
    }
    return (
      <div className="main-wrap page">
        <TopLine
          pageTitle="Персональный помощник"
          handleBack={this.goBackCustom}
        />
        <div className="container helper-wrap">
          <div className="helper-question">
            <div className="hq-title">
              Выберите, пожалуйста, что Вас интересует:
            </div>
            <ul>
              <li>
                <div className="helper-checkbox">
                  <input
                    type="radio"
                    name="quest"
                    data-quest="firstQuest"
                    onChange={this.getQuest}
                  />
                  <div className="helper-circle-wrap">
                    <div className="helper-circle"></div>
                  </div>
                </div>{" "}
                <label htmlFor="quest1">Не могу пополнить баланс</label>
              </li>
              <li>
                <div className="helper-checkbox">
                  <input
                    type="radio"
                    name="quest"
                    data-quest="secondQuest"
                    onChange={this.getQuest}
                  />
                  <div className="helper-circle-wrap">
                    <div className="helper-circle"></div>
                  </div>
                </div>{" "}
                <label htmlFor="quest2">Не могу пополнить баланс</label>
              </li>
            </ul>
            <button onClick={this.answerQuest}>Отправить</button>
          </div>
        </div>
        <div className="msg-wrap">
          <div className="answer" id="firstQuest">
            Вот ответ на Ваш вопрос #1
          </div>
          <div className="answer" id="secondQuest">
            Вот ответ на Ваш вопрос #2
          </div>
          {/* <div className="message message-left">
            <div className="msg-img">
              <img src={logo} alt="#" />
            </div>
            <div className="msg-text">
              <img src={triangle} alt="#" />
              Вас приветствует менеджер компании Flimcor. Чем могу почочь?
              <span>10:33</span>
            </div>
          </div>
          <div className="message message-right">
            <div className="msg-img">
              <img src={logo} alt="#" />
            </div>
            <div className="msg-text">
              <img src={triangle} alt="#" />
              Вас приветствует менеджер компании Flimcor. Чем могу почочь?
              <span>10:33</span>
            </div>
          </div> */}
        </div>
        <HelperBottom />
      </div>
    );
  }
}

export default Helper;
