import React, { Component } from "react";
import camera from "../../img/camera.png";
import { Link } from "react-router-dom";
import china from "../../img/china.png";
import ua from "../../img/ua.png";
import plane from "../../img/airplane-shape.svg";
import AddBtn from "./addBtn";

class LogisticBuybackItem extends Component {
  render() {
    return (
      <div
        className="delivery-item"
        style={{ opacity: this.props.item.delivered === true ? ".5" : "1" }}
      >
        <div className="delivery-info-wrap">
          <div className="delivery-image">
            <img src={require("../../img/delivery-item.jpg")} alt="#" />
            <img src={camera} alt="#" />
          </div>
          <div className="buying-right">
            <div className="delivery-main-info">
              <div className="delivery-title">
                CC-{this.props.item.order_id}
              </div>
              <ul>
                <li>Общее кол-во: 18</li>
                <li style={{ color: "#646464", marginTop: "5px" }}>
                  Косметика
                </li>
              </ul>
            </div>

            <div className="delivery-price">
              <Link to="#">Подробнее</Link>
              <br />
              <span>{this.props.item.total} USD</span>
            </div>
          </div>
        </div>
        <div className="delivery-date">
          <span>04.07.2019</span>
          <span
            style={{
              // color: this.props.item.gotten ? "#34bf9d" : "#007eff",
              color: "#007eff",
              fontWeight: "bold"
            }}
          >
            {/* {this.props.item.gotten ? "Товар получен" : "Доставляется"} */}
            {this.props.item.status_name}
          </span>
          <span>12.07.2019</span>
        </div>
        <div className="delivery-status">
          <img src={china} alt="#" />
          <div className="status-bar">
            <span
              style={{
                width: this.props.item.gotten ? "100%" : "33%",
                backgroundColor: this.props.item.gotten ? "#34bf9d" : "#007eff"
              }}
            >
              {" "}
              <i className="fas fa-shopping-cart" />
            </span>
          </div>
          <img src={ua} alt="#" />
        </div>
        <AddBtn linkTo="/cart" />
      </div>
    );
  }
}

export default LogisticBuybackItem;
